.card {
    border: 1px solid #eee;
    box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.5);
    padding: 10px;
    max-width: 400px;
    margin: 20px auto;
    border-radius: 8px;
   
}

.card button {
    background-color: #555;
    border: none;
    padding: 10px;
    border-radius: 15px;
    color: #fff;
}